<template>
    <div class="system-message-page">
        <navigation-bar  @onLeftClick="onBack" :pageName="title" :bothWidth="headerItemWidth" >
            <template v-slot:nav-right>
                <div class="rightBtn" @click="messageMarkRead">{{rightName}}</div>
            </template>
        </navigation-bar>

        <div class="message-list">
            <van-pull-refresh
            v-model="refreshing"
            @refresh="onRefresh" >
                <van-list
                    v-model="loading"
                    :error.sync="error"
                    error-text="请求失败，点击重新加载"
                    :finished="finished"
                    :finished-text="messageList.length > 0 ? '无更多数据...' : ''"
                    :offset="356"
                    :immediate-check="false"
                    @load="onLoad"
                    v-show="!(contentLoaded && messageList.length == 0)"
                >
                    <div class="message-cell" v-for="(item, index) in messageList " :key="index" @click="jumpNoticeContent(item, index)">
                        <img class="read-status-icon" src="../../assets/imgs/msg/ic_weidu@2x.png" v-if="item.isRead != 1" alt />
                        <div class="cell-top" :style="item.isRead != 1 ? 'padding-top: 3px' : 'padding-top: 15px'">
                          <div class="text-content">
                            <div class="text-title" :style="item.isRead != 1 ? 'color: #333333' : 'color: #C8C8C8'">{{item.systemName}}</div>
                          </div>
                          <div class="cell-right">
                            <div :style="item.isRead != 1 ? 'color: #9A9A9A' : 'color: #C8C8C8'">{{item.sendTime}}</div>
                          </div>
                        </div>
                        <div class="cell-bottom">
                            <div :style="item.isRead != 1 ? 'color: #333333; margin-bottom: 12px' : 'color: #C8C8C8; margin-bottom: 12px'">{{item.sendMessage}}</div>
                        </div>
                    </div>

                </van-list>
                    <div class="no-list" v-show="contentLoaded && messageList.length == 0">
                        <img :src="noSourceImg" alt="">
                    <div class="no-text">暂无消息</div>
                </div>
            </van-pull-refresh>
    </div>
    <!-- 弹出框 -->
    <van-dialog
    v-model="isShowDialog"
    confirmButtonColor="#00d8d1"
    :message="dialogMessage"
    show-cancel-button
    :beforeClose="beforeClose"/>
    </div>
</template>

<script>

    import {
        getSystemNotifyMessage,
        requestMessageReaded,
        getNoticeDetail,
        getUnreadCount
    } from "../../services/noticeService";


    import Vue from 'vue';
    import { Icon, List, PullRefresh } from 'vant';
    Vue.use(Icon).use(List).use(PullRefresh);
    import {storage, store} from '../../utils/store';

    import NavigationBar from '../title/NavigationBar';

    // 无内容图片
    import noSourceImg from '@/assets/imgs/workPlan/noSource.png'

    export default {
        name: "SystemNotice",
        components: {
            NavigationBar
        },
        created () {
            // this.getNoticeList(true);
        },
        activated() {
            if (this.$route.params.fromHome) {
                // enter msg list from home page mine, update userinfo and reset msg list first
                let custNews = store.get('custNews');
                this.userInfo = custNews ? custNews: storage.getStorage("custNews");
                this.pageNum = 1;
                this.currentItem = {};
                this.noteIndex = 0;
                this.messageList = [];
                this.scrollTop = 0;
                this.unReadMsgCount = 0;
                this.getNoticeList(true);
            } else {
                // from detail, do not update info, just update the current item
                document.querySelector(".message-list").scrollTop = this.scrollTop;
                this.updateCurrentNoticeInfo();
            }
            // 更新未读消息数目
            this.getUnreadCount();
        },
        data() {
            return {
                active: 0,
                title: '消息中心',

                rightName: '',
                headerItemWidth:{
                    width:'100PX'
                },

                messageList: [],
                refreshing: false,
                loading: false,
                finished: false,
                error: false,
                // 当前页码值
                pageNum: 1,
                // 每页个数
                pageSize: 10,
                // 内容已加载
                contentLoaded: false,

                //是否显示提示框
                isShowDialog: false,
                //提示框内容
                dialogMessage: '确认全部已读？',
                //设为已读消息id
                readedNotices:[],

                //图片
                noSourceImg: noSourceImg,
                userInfo: {},
                currentItem: {},
                noteIndex: 0,
                scrollTop: 0,
                unReadMsgCount: 0,
                unReadRefreshEnd: true,
                msgListRefreshEnd: true,
            };
        },

        methods: {
            onBack() {
                this.currentItem = {};
                this.noteIndex = 0;
                this.scrollTop = 0;
                this.unReadMsgCount = 0;
                this.$router.back(-1);
            },
            //设置消息全部为已读
            messageMarkRead() {
                if (this.messageList.length == 0) {
                    return;
                }
                else {
                    this.isShowDialog = true;
                }

            },
             // 退出登录弹框回调
            beforeClose: function(action, done) {
                if (action === "confirm") {
                    this.setMessageReaded();
                }
                done();
            },
            //设置消息已读
            setMessageReaded() {
                let requestData = {
                    "userCode": this.userInfo.USER_CODE,
                };
                requestMessageReaded(requestData)
                .then(noteData => {
                    if (noteData.resultCode == 0) {
                        this.showMsg(noteData.msg);
                        document.querySelector(".message-list").scrollTop = 0;
                        this.onRefresh();
                        this.unReadMsgCount = 0;
                        console.log("消息设置为全部已读成功。");
                    } else {
                        // 设置已读失败
                        console.log("消息设置已读失败。");
                    }
                })
                .catch(error => {
                    this.showMsg(error);
                });
            },
            //跳转到消息详情
            jumpNoticeContent(item, index) {
                this.currentItem = item;
                this.noteIndex = index;
                this.scrollTop = document.querySelector(".message-list").scrollTop;
                this.$router.push({
                    name: "NoticeDetail",
                    params: {noticeReq:  item}
                });
            },
            // 获取系统消息列表
            getNoticeList(isRefresh) {
                this.msgListRefreshEnd = false;
                let requestData = {
                    ownerCode: this.userInfo.USER_CODE,
                    page: this.pageNum,
                    rows: 10
                };
                getSystemNotifyMessage(requestData)
                .then(noteData => {
                    // console.log(JSON.stringify(noteData));
                    this.loading = false;
                    if (isRefresh) {
                        this.finished = false;
                        this.refreshing = false;
                        this.messageList = [];
                    }
                    if (noteData.resultCode == 0) {
                      if (noteData.data != undefined && noteData.data.list != undefined
                        && noteData.data.list.length > 0) {
                          noteData.data.list.forEach(element => {
                              this.messageList.push(element);
                          });
                          this.rightName = '全部标为已读';
                      }

                      // 加载完成
                      if (noteData.data && this.pageNum * this.pageSize >= noteData.data.total) {
                          this.finished = true;
                          this.loading = false;
                          this.refreshing = false;
                      }
                      this.pageNum++;
                      this.contentLoaded = true;
                    } else {
                      // 请求失败
                      this.loading = false;
                      this.refreshing = false;
                      this.error = true;
                      this.contentLoaded = true;
                      console.log("请求数据失败.");
                    }
                })
                .catch(error => {
                    this.loading = false;
                    this.refreshing = false;
                    this.error = true;
                    this.contentLoaded = true;
                    this.showMsg(error);
        //             let list = [
        //     {
        //         "messageId":50000,
        //         "messageTopic":"新帖子提醒",
        //         "systemType":"0005",
        //         "systemName":"发现",
        //         "sendMessage":"猪肉炖粉条：【图片】",
        //         "sendTime":"2020-01-31 13:56:20",
        //         "isRead":1
        //     },
        //     {
        //         "messageId":49997,
        //         "messageTopic":"新帖子提醒",
        //         "systemType":"0005",
        //         "systemName":"发现",
        //         "sendMessage":"猪肉炖粉条：【图片】",
        //         "sendTime":"2020-01-31 13:55:47",
        //         "isRead":0
        //     },
        //     {
        //         "messageId":49994,
        //         "messageTopic":"工单申请",
        //         "systemType":"0003",
        //         "systemName":"工单",
        //         "sendMessage":"您有新的来自【东篱CSS-1号楼-100】工单(361712070648926208)的变更申请，请及时处理",
        //         "sendTime":"2020-01-20 17:48:43",
        //         "isRead":0
        //     },
        //     {
        //         "messageId":49991,
        //         "messageTopic":"工单申请",
        //         "systemType":"0003",
        //         "systemName":"工单",
        //         "sendMessage":"您有新的来自【东篱CSS-null】工单(360668740641099776)的变更申请，请及时处理",
        //         "sendTime":"2020-01-20 17:26:13",
        //         "isRead":0
        //     },
        //     {
        //         "messageId":49988,
        //         "messageTopic":"工单申请",
        //         "systemType":"0003",
        //         "systemName":"工单",
        //         "sendMessage":"您有新的来自【东篱CSS-1号楼-100】工单(374835741450047488)的变更申请，请及时处理",
        //         "sendTime":"2020-01-20 17:25:10",
        //         "isRead":1
        //     },
        //     {
        //         "messageId":49985,
        //         "messageTopic":"工单指派",
        //         "systemType":"0003",
        //         "systemName":"工单",
        //         "sendMessage":"您有新的指派工单：360668740641099776，请尽快处理！",
        //         "sendTime":"2020-01-20 17:22:46",
        //         "isRead":0
        //     },
        //     {
        //         "messageId":49982,
        //         "messageTopic":"申请取消",
        //         "systemType":"0003",
        //         "systemName":"工单",
        //         "sendMessage":"来自【黑】对【东篱CSS】的【延时/修改分类】工单申请已取消，请知晓",
        //         "sendTime":"2020-01-20 17:22:20",
        //         "isRead":0
        //     },
        //     {
        //         "messageId":49980,
        //         "messageTopic":"工单指派",
        //         "systemType":"0003",
        //         "systemName":"工单",
        //         "sendMessage":"您有新的指派工单：374835741450047488，请尽快处理！",
        //         "sendTime":"2020-01-20 16:58:59",
        //         "isRead":0
        //     },
        //     {
        //         "messageId":49976,
        //         "messageTopic":"新帖子提醒",
        //         "systemType":"0005",
        //         "systemName":"发现",
        //         "sendMessage":"猪肉炖粉条：大王叫我来巡山",
        //         "sendTime":"2020-01-20 15:08:32",
        //         "isRead":0
        //     },
        //     {
        //         "messageId":49975,
        //         "messageTopic":"工单申请",
        //         "systemType":"0003",
        //         "systemName":"工单",
        //         "sendMessage":"您有新的来自【东篱CSS-1号楼-100】工单(361384723932516352)的变更申请，请及时处理",
        //         "sendTime":"2020-01-19 17:17:33",
        //         "isRead":0
        //     }
        // ];
        //             list.forEach(element => {
        //                     this.messageList.push(element);
        //                 });
        //                 this.rightName = '全部标为已读';
        //                 this.unReadMsgCount = 10;
                })
                .finally(() => {
                    if (this.unReadRefreshEnd) {
                        if (this.messageList.length > 0 && this.unReadMsgCount > 0 && !this.rightName) {
                            this.rightName = '全部标为已读';
                        } else if ((this.messageList.length <= 0 || this.unReadMsgCount <= 0) && this.rightName) {
                            this.rightName = '';
                        }
                    }
                    this.msgListRefreshEnd = true;
                });
            },
            // 加载更多
            onLoad() {
                if (!this.contentLoaded) {
                    return;
                }
                this.error = false;
                this.loading = true;
                this.getNoticeList(false);
            },
            //下拉刷新
            onRefresh() {
                this.error = false;
                this.loading = false;
                this.pageNum = 1;
                this.currentItem = {};
                this.noteIndex = 0;
                this.messageList = [];
                this.scrollTop = 0;
                this.unReadMsgCount = 0;
                this.getNoticeList(true);
                this.getUnreadCount();
            },
            showMsg: function(msg){
                this.$toast(msg);
            },
            updateCurrentNoticeInfo() {
                getNoticeDetail({id: this.currentItem.messageId})
                .then(result => {
                    //console.log(JSON.stringify(result));
                    if (result.resultCode == 0) {
                        this.$set(this.messageList, this.noteIndex, result.data);
                    } else {
                        console.log("update current msg failed.");
                    }
                })
                .catch(err => {
                    console.log(err);
                })
            },
            getUnreadCount() {
                this.unReadRefreshEnd = false;
                getUnreadCount({userCode: this.userInfo.USER_CODE})
                .then(result => {
                    if (result.resultCode == 0) {
                        // 成功
                        this.unReadMsgCount = result.data.unReadMsgCount;
                    } else {
                        // 失败
                        console.log("get unread msg count failed.");
                    }
                })
                .catch(err => {
                    console.log("get unread msg count failed for " + err);
                })
                .finally(() => {
                    if (this.msgListRefreshEnd) {
                        if (this.messageList.length > 0 && this.unReadMsgCount > 0 && !this.rightName) {
                            this.rightName = '全部标为已读';
                        } else if ((this.messageList.length <= 0 || this.unReadMsgCount <= 0) && this.rightName) {
                            this.rightName = '';
                        }
                    }
                    this.unReadRefreshEnd = true;
                });
            }
        }
    }
</script>

